import { mapMutations, mapState } from 'vuex';
export default {
	components: {
	},
	data() {
		return {
			localPop: false,
			pickIndex: 0,
			pickList: [{name: '不限'}],
		}
	},
	// 监听页面卸载
	onUnload() {
	},
	onLoad(){
		this.getLocalList();
	},
	onHide(){
	},
	computed: {
		...mapState(['mpInfo']),
	},
	methods: {
		...mapMutations(['login']),
		// 获取微信用户信息
		getWxUserInfo(){
			let that = this;
			uni.showLoading({
				title: '授权登录中'
			});
			uni.getUserInfo({
			  provider: 'weixin',
			  success: function (infoRes) {
				  that.Bmob.User.auth().then(res => {
					console.log('授权成功')
					const query = that.Bmob.Query('_User');
					query.get(res.objectId).then(res => {
						res.set('nickName',infoRes.userInfo.nickName)
						res.set('avatarUrl',infoRes.userInfo.avatarUrl)
						res.set('gender',infoRes.userInfo.gender)
						res.set('profile', '')
						res.set('praise', 0)
						res.set('follows', 0)
						res.set('fans', 0)
						res.save()
						that.login(res);	// 存储登录信息
						//更新登陆状态
						uni.setStorage({
						  key: 'userInfo',
						  data: res,
						  success: function () {
							  uni.hideLoading();
							  that.localPop = true;
						  }
						});
					  }).catch(err => {
					    console.log(err)
					  })
				  }).catch(err => {
					console.log(err)
					uni.showToast({
						title: '授权失败，请退出重试',
						icon: 'none'
					})
				  });
			  }
			});
		},
		// 获取地址列表
		getLocalList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('local');
			// 查询所有数据
			query.find().then(res => {
			  uni.hideLoading();
			  that.pickList = that.pickList.concat(res);
			});
		},
		changeLocal (event) {
			this.pickIndex = parseInt(event.currentTarget.dataset.name)
		},
		// 切换pick地址选择
		saveLocal() {
		 	let that = this;
		 	this.localId = this.pickList[parseInt(this.pickIndex)].objectId ? this.pickList[parseInt(this.pickIndex)].objectId : ''
		 	//更新地址
		 	uni.setStorage({
		 	  key: 'localId',
		 	  data: that.localId,
		 	  success: function () {
		 		  console.log('更新地址成功')
				  uni.reLaunch({
					  url: '/pages/index/index'
				  });
		 	  }
		 	});
		 },
	}
}
